import $ from 'jquery';

export default () => {
    $(function () {
        let flag;
        let elmBody = $('body');
        const navMobile = '.js-nav-menu';
        const $navMobile = $('.js-nav-menu');
        const $navItem = $navMobile.find('.js-menu-item');
        const $iconBack = $navMobile.find('.js-icon-back');
        const subMenu = '.js-sub-menu';
        const clasActive = 'fade_in';
        const $loading = $("#loading");        
        
        $(document).ready(function () {
            $loading.hide();
        });
   
        $navItem.on('click', function () {
            const $this = $(this);
            const $subMenu = $this.find(subMenu);
            if ($subMenu.length) {
                $subMenu.addClass(clasActive);
                $iconBack.addClass(clasActive);
            }
        });

        $iconBack.on('click', function() {
            const $this = $(this);
            $this.removeClass(clasActive);
            $this
                .closest(navMobile)
                .find(subMenu)
                .removeClass(clasActive);
        });

        $('.nav-icon').click((event) => {
            $('.nav-menu').css("width", "100%");
            elmBody.addClass('hidden-body');
            flag = 1;
        })

        $('.icon-close').click(() => {
            $('.nav-menu').css("width", "0");
            elmBody.removeClass('hidden-body');
            flag = 0;
        })

        $(window).resize(function () {
            if (window.innerWidth > 1024) {
                $('.nav-menu').css("width", "0");
            } else {
                if (flag) {
                    $('.nav-menu').css("width", "100%");
                }
            }
        });

        function activeMenuItem(link) {
            let item_links = $('div.item-link').parent().siblings();
            // remove all li tag have class 'active' in nav bar
            item_links.removeClass('active');
            item_links.find(`a[href="/${link}"]`).parents('li').addClass('active');
        }

        //get the final path in the url
        let currentPage = '';
        const pathName = document.location.pathname;
        const pathNameArr = pathName.split('/');
        if (pathNameArr.length) {
            currentPage = pathNameArr[pathNameArr.length - 1];
        }
        
                
        const parentOfCurrentPage = $('.breadcrumb-list').find('li:first-child').find('a').attr('href');
        const parentOfCurrentPageName = parentOfCurrentPage ? parentOfCurrentPage.split('/')[1] : null;
        activeMenuItem(parentOfCurrentPageName || currentPage);
    });
};