import $ from 'jquery';
import { loadJSWithPage } from '../utils/helpers';
import { element } from 'svelte/internal';

export default loadJSWithPage(() => {
    $(function() {
        const { phasesData, programsData, _year, _phaseId, _programId} = window.EKINO_ACADEMY.page.enrol;
        const $loading = $('#loading');
        $(document).ready(function() {
            $loading.hide();
        });
        function getTextSelectProgram() {
            let textOption = $('#select-program option:selected').text();
            $('.select-value-program').html(textOption);
        }

        if (_year !== 'NA') {
            $('#select-year').val(_year).change();
        }
        if (_phaseId !== 'NA') {
            $('#select-phase').val(_phaseId).change();
        }
        if (_programId !== 'NA') {
            $('#select-program').val(_programId).change();
        }

        function validateRangeTimeEnrol() {
            let currentTime = new Date().getTime();
            let data = programsData.filter(elm => {
               let startDate = new Date(elm.startDate.date.year, elm.startDate.date.month -1, elm.startDate.date.day + 1).getTime();
               let endDate = new Date(elm.endDate.date.year, elm.endDate.date.month -1, elm.endDate.date.day + 1).getTime();
               return (startDate <= currentTime && currentTime <= endDate);
            });
            if(data.length === 0){
                window.location.replace('/result?code=CMP_003')
            }
        
        }
        validateRangeTimeEnrol()

        function getTextSelectPhase() {
            let textOption = $('#select-phase option:selected').text();
            $('.select-value-phase').html(textOption);
        }

        function changeSelectedPhase() {
            let elmPhase = $('#select-phase');
            elmPhase.attr('disabled', true);
            $('.select-value-phase').addClass('unselected');
            elmPhase.val('').change();
            getTextSelectPhase();
        }

        function changeSelectedProgram() {
            let elmProgram = $('#select-program');
            elmProgram.attr('disabled', true);
            $('.select-value-program').addClass('unselected');
            elmProgram.val('').change();
            getTextSelectProgram();
        }

        function setOptionsPhase(valueYear) {
            let getOptionsPhase = [];
            let arrOptions = [];
            getOptionsPhase.push({
                value: '',
                label: '- - -',
            });
            phasesData.forEach(element => {
                let getStartYear = element.startDate.date.year.toString();
                let getEndYear = element.endDate.date.year.toString();
                let getPhase = element.nodeName;
                let getUid = element.uuid;
                if (getStartYear === valueYear || getEndYear === valueYear) {
                    arrOptions = getOptionsPhase.map(elm => elm.value);
                    if (!arrOptions.includes(getPhase)) {
                        getOptionsPhase.push({
                            value: getUid,
                            label: getPhase,
                        });
                    }
                }
            });
            return renderOptions(
                'select-phase',
                getOptionsPhase
            );
        }
        function setOptionsProgram(valuePhase) {
            let getOptionsProgram = [];
            let arrOptions = [];
            arrOptions.push({
                value: '',
                label: '- - -',
            });
            programsData.forEach(element => {
                let getIdPhase = element.phase.uuid;
                if (getIdPhase === valuePhase) {
                    let getProgram = element.nodeName;
                    let getUid = element.uuid;
                    getOptionsProgram = arrOptions.map(elm => elm.value);
                    if (!getOptionsProgram.includes(getProgram)) {
                        arrOptions.push({
                            value: getUid,
                            label: getProgram,
                        });
                    }
                }
            });
            return renderOptions(
                'select-program',
                arrOptions
            );
        }

        function renderOptions(idName, listOption) {
            let tmpOption = '';
            for (let i = 0; i < listOption.length; i++) {
                let option = listOption[i];
                tmpOption += `
            <option value= "${option.value}" >${option.label}</option>            
            `;
            }
            $(`#${idName}`).html(tmpOption);
        }

        let setOptionsYear = () => {
            let arrOptions = [];
            let arrYears = [];
            let currentYear = new Date().getFullYear();
            arrOptions.push({
                value: '',
                label: '- - -',
            });
            phasesData.forEach(element => {
                let getStartYear = element.startDate.date.year;
                let getEndYear = element.endDate.date.year;
                if(!arrYears.includes(getStartYear) && (getStartYear >= currentYear)){
                    arrYears.push(getStartYear)
                }
                if(!arrYears.includes(getEndYear) && (getEndYear >= currentYear)){
                    arrYears.push(getEndYear)
                }                
                    return arrYears;
            });
            arrYears.forEach(year => {
                arrOptions.push({
                    value: year,
                    label: year
                })
            })
            return arrOptions;
        };
        let optionsYear = setOptionsYear();
        let renderOptionsYear = renderOptions('select-year', optionsYear);
        renderOptionsYear;

        $('#select-phase').change(event => {
            let value = event.target.value.toLowerCase();
            setOptionsProgram(value)
            getTextSelectPhase();
            changeSelectedProgram();
            if (value) {
                $('.btn__enrol').attr('disabled', true)
                $('#select-program').attr('disabled', false);
                $('.select-value-program').removeClass('unselected');
            }
        });

        $("#enrol-form").submit( function(event) {
            let labelSelected = $('#select-program option:selected').text();
            $('#enrol-form').append(`<input type="hidden" name="programName" value= "${labelSelected}" /> `);
            return true;
        });

        $('#select-program').change(event => {
            getTextSelectProgram();
        });

        $('#select-year').change(event => {
            let value = event.target.value;
            let textOption = $('#select-year option:selected').text();
            $('.select-value-year').html(textOption);
            setOptionsPhase(value);
            changeSelectedPhase();
            changeSelectedProgram();
            if (value) {
                $('.btn__enrol').attr('disabled', true)
                $('#select-phase').attr('disabled', false);
                $('.select-value-phase').removeClass('unselected');
            }
        });

        $('#enrol-eye-password').click(() => {
            let elm = $('#enrol-eye-password');
            if (elm.hasClass('active')) {
                elm.removeClass('active');
                $('#enrol-password').attr('type', 'password');
            } else {
                elm.addClass('active');
                $('#enrol-password').attr('type', 'text');
            }
        });

        $('#enrol-eye-confirm').click(() => {
            let elm = $('#enrol-eye-confirm');
            if (elm.hasClass('active')) {
                elm.removeClass('active');
                $('#enrol-confirm').attr('type', 'password');
            } else {
                elm.addClass('active');
                $('#enrol-confirm').attr('type', 'text');
            }
        });
    });
}, 'enrol');
