export function loadJSWithPage(func, page) {
  const keyPage = "dev-page"
  const devPage = window.EKINO_ACADEMY.common.queryParams(keyPage)
  const currentPage = window.EKINO_ACADEMY['currentPage'] 
  
  if(currentPage === page || page === devPage[keyPage]) {
    return func
  }

  return () => {}
}

