import $ from 'jquery';
import { loadJSWithPage } from '../utils/helpers'

export default loadJSWithPage(() => {
    $(document).ready(function () {
        const $loading = $("#loading");
        $(document).ready(function () {
            $loading.hide();
        });
        $('.wrapper-goTop').click(() => {
            $('html,body').animate({
                scrollTop: 0
            }, 300);
        });
    });
    
}, "onJobTraining");