import $ from 'jquery';
import Scrollbar from 'smooth-scrollbar/dist/smooth-scrollbar';
import 'twbs-pagination/jquery.twbsPagination';
import Choices from 'choices.js';
import { loadJSWithPage } from '../utils/helpers';

export default loadJSWithPage(() => {
    $(function() {
        const {
            data,
            dataYears,
            roles,
            fullNameOfCurrentUser,
            dataDates,
        } = window.EKINO_ACADEMY.page.courseStatus;
        const $body = $('body');
        const $document = $(document);
        const $loading = $('#loading');
        const $course = $('.js-course');
        const $formProgram = $course.find('.js-form-program');
        const $formProgramWrapper = $formProgram.closest(
            '.section-form__program'
        );
        const $input = $course.find('.js-course-input');
        const $listItems = $course.find('.js-course-items');
        const $infoWrapper = $course.find('.js-info-wrapper');
        const $searchBox = $course.find('.js-course-search-box');
        const $switchWrapper = $course.find('.js-course-switch');
        const $switchRole = $switchWrapper.find('.js-switch__checkbox');
        const item = '.js-item';
        const $itemWrapper = $course.find('.js-course-block');
        const $timeLine = $('#course-status-slide');
        const $btnPhases = $course.find('.js-item-phase');
        const selection = new Choices('#semester-dropdown', {
            itemSelectText: '',
        });
        const selectSemester = $('#semester-dropdown');
        const selectClass = $course.find('.dropdown-semester');
        let getIdPhaseInLocalStorage = localStorage.getItem('idPhase');
        let getIdProgramInLocalStorage = localStorage.getItem('listIdChecked');
        let dataCourse;
        let dataProgramID;
        let dataCourseID;
        let prevIdProgram = 0;
        let prevIdPhases = 0;
        let dataPhases = [];
        let listCoursesOfPhase = [];
        let listCoursesSemesterFilter = [];
        let dataPhaseGeted = [];
        let dataProgramsGeted = [];
        let idProgram = '';
        let listIdChecked = '';
        let semesterChanged = '';
        const $evnReadMore = $('.read-more');
        const query = window.EKINO_ACADEMY.common.queryParams();
        const PER_PAGE = 10;

        const getParam = () => {
            const { year, phase, program } = query;
            if (year && phase && program) {
                return query;
            } else {
                return false;
            }
        };

        const setAttrDisabled = (listElement, value) => {
            for (let i = 0; i < listElement.length; i++) {
                listElement[i].prop('disabled', value);
            }
        };

        const getDataById = (id, dataId, data) => {
            return data.filter(e => e[dataId] === id)[0];
        };
        const handleRoles = () => {
            if (roles.includes('trainer')) {
                $switchWrapper.show();
                $switchRole.prop('checked', false);
                $('.js-switch__checkbox').attr('disabled', true);
            }
            if (roles.includes('supervisor')) {
                $switchWrapper.show();
                $switchRole.prop('checked', true);
                $('.js-switch__checkbox').attr('disabled', true);
            }
            if (roles.includes('supervisor') && roles.includes('trainer')) {
                $switchWrapper.show();
                $switchRole.prop('checked', true);
                $('.js-switch__checkbox').attr('disabled', false);
            }
        };

        const renderProgram = (listProgram, dataDate) => {
            const params = getParam();
            const htmlProgram = listProgram.reduce((acc, programs) => {
                const isHasParam = params ? programs.id === params.program : '';
                let arrDate1 = [];
                let arrDate2 = [];
                let date1;
                let date2;
                dataDate.forEach(e => {
                    if (e.programId === programs.id) {
                        e.semesterDates.forEach(elm => {
                            if (elm.includes('Semester 2')) {
                                arrDate2.push(elm);
                            } else {
                                arrDate1.push(elm);
                            }
                        });
                        date1 = arrDate1.toString().split(';');
                        date2 = arrDate2.toString().split(';');
                    }
                });
                const checked = listProgram.length || isHasParam;
                return (acc += `
                    <label class="wrapper-item">
                        <input
                        type="checkbox"
                        name="checkProgram"
                        class="js-item"
                        data-id=${programs.id}
                        data-type=program
                        checked = ${checked && 'checked'}
                        id="program-item-${programs.id}"
                        />
                        <span class="checkmark"></span>
                        <div class="radio-item">
                            <p class="radio__title">
                                ${programs.name}
                            </p>
                            <p class="radio__time">
                            ${date1[0]}:
                            <span>${date1[1]} - ${date1[2]}</span>
                            </p>
                            <p class="radio__time">
                            ${date2[0]}:
                            <span>${date2[1]} - ${date2[2]}</span>
                            </p>
                        </div>
                    </label>
                    `);
            }, '');
            $formProgram.html(htmlProgram);
            $formProgramWrapper.show();
            selectClass.show();
            $searchBox.css({ display: 'flex' });
            setAttrDisabled([$input, $switchRole], false);
            handleRoles();
            if (listProgram.length === 1) {
                const uidProgram = listProgram[0].id;
                renderCourses(listCoursesOfPhase);
                prevIdProgram = uidProgram;
            }
            if (params || listProgram.length === 1) {
                handleRoles();
            }
            if (query.program) {
                handleContentTypeParamUrlProgram();
            } else {
                getProgramChecked();
            }
        };

        const resetData = () => {
            $input.val('');
            $infoWrapper.html('');
            $infoWrapper.hide();
            $listItems.html('');
        };

        const renderParticipants = listParticipant => {
            const htmlPaticipants = listParticipant.reduce((acc, e) => {
                let numberOfQuizzes = e.numberOfQuizzes;
                if(!numberOfQuizzes) {
                    numberOfQuizzes = 1;
                }
                let elmOfQuizzes = e.quizzes;
                if(!elmOfQuizzes) {
                    elmOfQuizzes = [];
                }
                let tmpQuiz = '';
                for(let idxNumQuiz = 0; idxNumQuiz < numberOfQuizzes; idxNumQuiz++) {
                    let quiz = elmOfQuizzes.filter((item) => {
                        let newIdxNumQuiz = String(idxNumQuiz + 1)
                        return item.quizNo === newIdxNumQuiz;
                    });
                    if (quiz.length && quiz[0].quizScore) {
                        tmpQuiz += `<div class="quizzes__score">${quiz[0].quizScore}</div>`
                    } else {
                        tmpQuiz += `<div class="quizzes__score">-</div>`
                    }
                }
                    acc += `
                    <tr>
                        <td data-label="Name">
                            ${e.fullName}
                        </td>
                        <td data-label="Attendances">
                            ${e.attendant === null ? '-' : e.attendant}
                        </td>
                        <td data-label="Quiz" class="quizzes">
                            <div class="quizzes__content">
                                ${tmpQuiz}
                            </div>
                        </td>
                        <td data-label="Homework">
                            ${e.homework === null ? '-' : e.homework}
                        </td>
                        <td data-label="Score" class="bold-score">
                            ${e.score === null ? '-' : e.score}
                        </td>
                    </tr>
                `;
                return acc;
            }, '');
            $('#js-scroll-table').html(htmlPaticipants);
            orderStatus(0, 'text');
            orderDashInStatus();
        };

        const renderInfo = course => {
            dataCourse = course;
            let getUriName = course.uriName;
            let path = '/course-status/course-detail/' + getUriName;
            const htmlInfo = `
        <div class="course__info">
        <div class="evn-row">
            <div class="evn-col evn-col-lg--12 evn-col--12">
                <div class="evn-row js-course-info">
                    <div class="course__info__col evn-col-lg--8 evn-col--12">
                        <span class="course__info__label course__label">Course Name:</span>
                        <span class="course__info__value">${
                            course.courseName
                        }</span>
                    </div>
                    <div class="course__info__col evn-col-lg--4 evn-col--12">
                        <span class="course__info__label course__label">Semester:</span>
                        <span class="course__info__value">${
                            course.semester
                        }</span>
                    </div>
                    <div class="course__info__col evn-col-lg--2 evn-col--12">
                        <span class="course__info__label course__label">Duration:</span>
                        <span class="course__info__value">${
                            course.duration
                        }<span class="course__info__sub-value">
                                Hours</span></span>
                    </div>
                    <div class="course__info__col evn-col-lg--4 evn-col--12">
                        <span class="course__info__label course__label">Compulsory:</span>
                        <span class="course__info__value">${
                            course.compulsory ? 'Yes' : 'No'
                        }</span>
                    </div>
                    <div class="course__info__col evn-col-lg--8 evn-col--12">
                        <span class="course__info__label course__label">Status:</span>
                        <div class="course__info__progress wrapper-progress status-${course.status.toLowerCase()}">
                            <div class="progress-item">
                                <div class="progress progress--borderLeft">
                                    <div class="progress-status"></div>
                                </div>
                                <p class="progress__lable">open</p>
                            </div>
                            <div class="progress-item">
                                <div class="progress">
                                    <div class="progress-status"></div>
                                </div>
                                <p class="progress__lable">class</p>
                            </div>
                            <div class="progress-item">
                                <div class="progress">
                                    <div class="progress-status"></div>
                                </div>
                                <p class="progress__lable">quiz</p>
                            </div>
                            <div class="progress-item">
                                <div class="progress">
                                    <div class="progress-status"></div>
                                </div>
                                <p class="progress__lable">homework</p>
                            </div>
                            <div class="progress-item">
                                <div class="progress progress--borderRight">
                                    <div class="progress-status"></div>
                                </div>
                                <p class="progress__lable">finished</p>
                            </div>
                        </div>
                    </div>
                    <div class="course__info__col evn-col-lg--4 evn-col--12">
                        <span class="course__info__label course__label">Trainer:</span>
                        <span class="course__info__value">${
                            course.trainer
                        }</span>
                    </div>
                    <div class="course__info__col evn-col-lg--4 evn-col--12">
                        <span class="course__info__label course__label">Supervisor:</span>
                        <span class="course__info__value">${
                            course.supervisor
                        }</span>
                    </div>
                    <div class="course__info__col evn-col-lg--12 evn-col--12">
                        <div class="course__info__description">
                            <span class="course__info__label course__label">Description:</span>
                            <span class="course__info__value">${
                                course.desc
                            }</span>
                        </div>
                        <div class="course__info-button">
                            <a class="evn-btn" href="${path}">Details</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        </div>
        <div class="course__participants js-participants">
        <div class="course__label">participants:</div>
        <div class="program-tab__content__item">
            <div class="course__input__wrapper js-participants-search">
                <span class="course__input__line"></span>
                <input type="text" class="course__input__text" id="search__participants__input"
                    placeholder="Search Name here" />
            </div>
            <div class="table-session table-courses">
            <table class="table-content table">
                <thead>
                    <tr>
                        <th class="sort__name">Name
                        </th>
                        <th>Attendances</th>
                        <th>Quiz</th>
                        <th>Homework</th>
                        <th class="sort__score js-sort__score" id="sort-score">Score
                        <span class="arrow-top js-arrow-up"></span>
                        <span class="arrow-bottom js-arrow-down"></span>
                        </th>
                    </tr>
                </thead>
                <tbody class="scroll-table" id="js-scroll-table">
                </tbody>
            </table>
        </div>
        </div>
        </div>
            `;
            $infoWrapper.html(htmlInfo);
            renderParticipants(course.participants);
            $infoWrapper.show();
            $('.js-list-course').hide();
            selectClass.hide();
            searchParticipants();
            orderStatus(0, 'text');
            handleSortScore();
            orderDashInStatus();
            $('.pagination__wrapper').hide();
        };
        const handleSortScore = () => {
            let $sortScore = $('#sort-score');
            $sortScore.click(function() {
                let $arrowUpCurrent = $(this).children('.js-arrow-up');
                let $arrowDownCurrent = $(this).children('.js-arrow-down');
                orderStatus(4, 'number');
                if (
                    !$arrowUpCurrent.hasClass('arrow-opacity') &&
                    !$arrowDownCurrent.hasClass('arrow-opacity')
                ) {
                    $arrowDownCurrent.addClass('arrow-opacity');
                } else {
                    if ($arrowUpCurrent.hasClass('arrow-opacity')) {
                        $arrowUpCurrent.removeClass('arrow-opacity');
                        $arrowDownCurrent.addClass('arrow-opacity');
                    } else {
                        $arrowUpCurrent.addClass('arrow-opacity');
                        $arrowDownCurrent.removeClass('arrow-opacity');
                    }
                }
            });
        };

        const renderCourses = listCourses => {
            const htmlCourse = listCourses.length
                ? listCourses.reduce((acc, e) => {
                      return (acc += `<li class="course__input__item js-item" data-id=${e.courseId} data-type=course>${e.courseName}</li>`);
                  }, '')
                : `<li class="course__input__item">Not found</li>`;
            $listItems.html(htmlCourse);
        };

        const initProgram = idPhases => {
            const getPhases = getDataById(idPhases, 'id', dataPhases);
            dataProgramID = getPhases.programs;
            renderProgram(dataProgramID, dataDates);
        };

        const getData = target => {
            const $this = $(target);
            const result = {
                uid: $this.data('id'),
                type: $this.data('type'),
            };
            return result;
        };

        let renderListCourses = (data, currentPage) => {
            let tmp = '';
            data.forEach((data, index) => {
                tmp += `<div class="project__item">
                                <div class="project-item-wrapper">
                                    <div class="item__title">
                                        <span class="item__number">${(currentPage -
                                            1) *
                                            10 +
                                            index +
                                            1}</span>
                                        <span class="item__lable">${
                                            data.courseName
                                        }</span>
                                    </div>
                                    <div class="item__content">
                                        <div class="read-more">
                                            <a href="#" class="js-read-more" data-courseid="${
                                                data.courseId
                                            }">Read more</a>
                                        </div>
                                        <div class="wrapper-item status-${data.status.toLowerCase()}">
                                            <div class="wrapper-progress">
                                                <div class="progress-item">
                                                    <div class="progress progress__open progress--borderLeft">
                                                        <div class="progress-status"></div>
                                                    </div>
                                                    <p class="progress__lable">open</p>
                                                </div>
                                                <div class="progress-item">
                                                    <div class="progress progress__class">
                                                        <div class="progress-status"></div>
                                                    </div>
                                                    <p class="progress__lable">class</p>
                                                </div>
                                                <div class="progress-item">
                                                    <div class="progress progress__quiz">
                                                        <div class="progress-status"></div>
                                                    </div>
                                                    <p class="progress__lable">quiz</p>
                                                </div>
                                                <div class="progress-item">
                                                    <div class="progress progress__homework">
                                                        <div class="progress-status"></div>
                                                    </div>
                                                    <p class="progress__lable">h-work</p>
                                                </div>
                                                <div class="progress-item">
                                                    <div class="progress progress--borderRight progress__finished">
                                                    </div>
                                                    <p class="progress__lable">finished</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>`;
            });
            $('.js-list-course').html(tmp);
        };

        let getCourses = data => {
            let getYears = window.EKINO_ACADEMY.common.queryParams().year;
            if (!getYears) {
                getYears = new Date().getFullYear();
            }
            let getPhases = $btnPhases.data('id');
            let arr = [];
            let arrPhase = [];
            if (data) {
                if (getYears) {
                    if (data.year == getYears) {
                        arr.push(data);
                    }
                }
                if (getPhases) {
                    let arrYear = arr;
                    arrYear.forEach(e => {
                        dataPhases = e.phases;
                        e.phases.forEach(e2 => {
                            if (e2.id == getPhases) {
                                arrPhase.push(e2);
                            }
                        });
                    });
                }
            }
            return arr;
        };
        getCourses(data);

        let dataPrograms = arrdata => {
            let allPrograms = [];
            arrdata.forEach(e => {
                e.programs.forEach(e1 => {
                    allPrograms.push(e1);
                });
            });
            return allPrograms;
        };

        let getCoursesOfTrainer = dataListCourse => {
            return dataListCourse.filter(e => {
                return e.trainer === fullNameOfCurrentUser;
            });
        };

        let getPhaseId = idData => {
            return dataPhases.filter(e => {
                return e.id === idData;
            });
        };

        let getProgramId = idData => {
            return dataProgramsGeted.filter(e => {
                return e.id === idData;
            });
        };

        let getCoursesOfPhase = data => {
            let allCoursesTypePhases = [];
            data.forEach(e => {
                e.programs.forEach(e1 => {
                    e1.courses.forEach(e2 => {
                        allCoursesTypePhases.push(e2);
                    });
                });
            });
            return allCoursesTypePhases;
        };

        let unCheck = () => {
            if (idProgram.length > 1) {
                $("input[name='checkProgram']").prop('checked', true);
            }
        };

        let removeDuplicateOption = arr => {
            let filteredArr = arr.reduce((acc, current) => {
                const x = acc.find(e => e.value === current.value);
                if (!x) {
                    return acc.concat([current]);
                } else {
                    return acc;
                }
            }, []);
            return filteredArr;
        };

        function handleContentTypeParamUrlPhase() {
            if (query.phase) {
                $infoWrapper.hide();
                $btnPhases.each((_, elm) => {
                    let value = $(elm).data('id');
                    if (value === query.phase) {
                        $(elm).addClass('active');
                    }
                });
                let dataPhaseTypeParamUrl = getPhaseId(query.phase);
                handleContentPhase(dataPhaseTypeParamUrl, query.phase);
            }
            if (getIdPhaseInLocalStorage && !query.phase) {
                $btnPhases.each((_, elm) => {
                    let value = $(elm).data('id');
                    if (value === getIdPhaseInLocalStorage) {
                        $(elm).addClass('active');
                    }
                });
                let dataPhaseTypeParamUrl = getPhaseId(
                    getIdPhaseInLocalStorage
                );
                handleContentPhase(
                    dataPhaseTypeParamUrl,
                    getIdPhaseInLocalStorage
                );
            }
        }
        handleContentTypeParamUrlPhase();

        function handleContentTypeParamUrlProgram() {
            if (query.program) {
                $('.js-item').each((_, elm) => {
                    let value = $(elm).data('id');
                    if (value === query.program) {
                        $(elm).prop('checked', true);
                    } else {
                        $(elm).prop('checked', false);
                    }
                });
                handleContentProgram();
            }
        }

        function handleContentProgram() {
            getProgramChecked();
            let dataOption = formatSelectSemester(getCourseProgramsChecked());
            let optionSelect = removeDuplicateOption(dataOption);
            setOptionSelect(optionSelect);
            initPagination(getCourseProgramsChecked(), PER_PAGE);
        }

        function handleContentPhase(dataPhase, idPhase) {
            dataProgramsGeted = dataPrograms(dataPhase);
            listCoursesOfPhase = getCoursesOfPhase(dataPhase);
            dataCourseID = listCoursesOfPhase;
            localStorage.setItem('idPhase', `${idPhase}`);
            let dataOption = formatSelectSemester(listCoursesOfPhase);
            let option = removeDuplicateOption(dataOption);
            setOptionSelect(option);
            initProgram(idPhase);
        }

        function handleDefaultDisplay() {
            if (!query.phase) {
                if (!getIdPhaseInLocalStorage) {
                    $('.js-item-phase:first-child').addClass('active');
                    handleContentPhase([dataPhases[0]], dataPhases[0].id);
                }
            }
        }
        handleDefaultDisplay();

        const handlePhases = () => {
            $btnPhases.click(function() {
                $infoWrapper.hide();
                $('.js-list-course').show();
                $('.js-course-input').val('');
                selectClass.show();
                $('.pagination__wrapper').show();
                unCheck();
                //----idProgram null when from Program jump to Phase
                idProgram = '';
                let clickedPhases = $(this).data('id');
                selection.setChoiceByValue('all');
                selection.clearChoices();
                localStorage.clear('courseId');
                localStorage.setItem('idPhase', `${clickedPhases}`);
                let dataIdPhase = getPhaseId(clickedPhases);
                dataPhaseGeted = dataIdPhase;
                listCoursesOfPhase = getCoursesOfPhase(dataIdPhase);
                dataCourseID = listCoursesOfPhase;
                listCoursesSemesterFilter = dataCourseID;
                dataProgramsGeted = dataPrograms(dataIdPhase);
                let dataOption = formatSelectSemester(listCoursesOfPhase);
                let option = removeDuplicateOption(dataOption);
                setOptionSelect(option);
                const { uid } = getData(this);
                if (prevIdPhases !== uid) {
                    $btnPhases.removeClass('active');
                    $(this).addClass('active');
                    resetData();
                    initProgram(uid);
                    prevIdPhases = uid;
                }
                listIdChecked = [];
                $.each($("input[name='checkProgram']:checked"), function() {
                    listIdChecked.push($(this).data('id'));
                });
                //----clickedPhases null when from Phase jump to Phase
                clickedPhases = '';
                //reset pagnumber correct with total course of semester
                initPagination(listCoursesOfPhase, PER_PAGE);
                localStorage.setItem('idProgram', `${listIdChecked}`);
            });
        };

        const handleListSearchCourse = courseId => {
            const course = getDataById(courseId, 'courseId', dataCourseID);
            renderInfo(course);
            $itemWrapper.hide();
            localStorage.setItem('courseId', `${courseId}`);
        };

        function handleFilterTypeTrainer(listCourses) {
            let getChecked = $switchRole.prop('checked');
            const data =
                getChecked === false
                    ? getCoursesOfTrainer(listCourses)
                    : listCourses;
            return data;
        }

        function handleRenderCourseFinal(data) {
            const currentPage = $('.page-item.active')
                .find('a')
                .text();
            renderListCourses(data, currentPage);
            renderCourses(data);
        }

        function initPagination(data, itemPerPage) {
            $('.pagination__wrapper').twbsPagination('destroy');
            let listCourseFinal = handleFilterTypeTrainer(data);
            if (listCourseFinal.length) {
                let numberPage = listCourseFinal.length / itemPerPage;
                if (listCourseFinal.length % itemPerPage) {
                    numberPage += 1;
                }
                $('.pagination__wrapper').twbsPagination({
                    nextClass: 'page-items next',
                    prevClass: 'page-items prev',
                    lastClass: 'page-items last',
                    firstClass: 'page-items first',
                    first: '&#171;',
                    prev: '&#8249;',
                    next: '&#8250;',
                    last: '&#187;',
                    currentPage: 1,
                    totalPages: numberPage,
                    visiblePages: 5,
                    onPageClick: function(event, page) {
                        const startIndex = (page - 1) * 10;
                        const endIndex = page * 10;
                        const listShowOnePage = listCourseFinal.slice(
                            startIndex,
                            endIndex
                        );
                        handleRenderCourseFinal(listShowOnePage);
                    },
                });
            } else {
                handleRenderCourseFinal(listCourseFinal);
            }
        }

        initPagination(listCoursesOfPhase, PER_PAGE);

        function handleDisplayCourseInfo() {
            let getCourseIdInLocalStorage = localStorage.getItem('courseId');
            let dataIdPhase = getPhaseId(getIdPhaseInLocalStorage);
            let listCourse = getCoursesOfPhase(dataIdPhase);
            if (listCourse !== null && listCourse.length && !query.phase) {
                if (
                    getCourseIdInLocalStorage !== null &&
                    getCourseIdInLocalStorage.length
                ) {
                    const dataCourseCurrent = listCourse.find(
                        ({ courseId }) => courseId === getCourseIdInLocalStorage
                    );
                    $('.pagination__wrapper').hide();
                    renderInfo(dataCourseCurrent);
                } else {
                    return false;
                }
            }
        }
        handleDisplayCourseInfo();

        function clearLocalStorage(element) {
            $(element).on('click', function(event){
                localStorage.clear('courseId');
            })
        }
        clearLocalStorage('.sub-menu-link');
        clearLocalStorage('.item-link');
        clearLocalStorage('.app-header__academylogo');
        clearLocalStorage('.js-slide-year');
        clearLocalStorage('.item-profile');
        clearLocalStorage('.app-footer__wrapper--left');

        const dataSwitchChange = () => {
            let dataSwitch = [];
            if (!semesterChanged) {
                dataSwitch = getCourseProgramsChecked();
            } else {
                dataSwitch = listCoursesSemesterFilter;
            }
            return dataSwitch;
        };

        const handleSwitch = () => {
            $switchRole.change(() => {
                resetData();
                initPagination(dataSwitchChange(), PER_PAGE);
            });
        };

        const handleSearchClick = () => {
            $input.on('click keyup', function(e) {
                const keyCode = e.keyCode;
                if (keyCode && dataCourseID.length) {
                    const keyword = e.currentTarget.value.toLowerCase();
                    const listResult = dataCourseID.filter(
                        e => e.courseName.toLowerCase().search(keyword) !== -1
                    );
                    renderCourses(listResult);
                } else {
                    $itemWrapper.show();
                }
            });
        };

        const handleUnFocus = () => {
            $document.click(e => {
                const isInput = $(e.target).is(
                    "input[type='text'].js-course-input"
                );
                const isCourse = $(e.target).is('li.js-item');
                if (!isInput && !isCourse) {
                    $itemWrapper.hide();
                }
            });
        };
        function getProgramChecked() {
            let arrIdChecked = [];
            listIdChecked = arrIdChecked;
            $.each($("input[name='checkProgram']:checked"), function() {
                arrIdChecked.push($(this).data('id'));
            });
        }
        function getCourseProgramsChecked() {
            let listProgramsChecked = [];
            let listCoursesChecked = [];
            listIdChecked.forEach(e => {
                listProgramsChecked.push(getProgramId(e));
            });
            listProgramsChecked.forEach(e => {
                listCoursesChecked = [...listCoursesChecked, ...e[0].courses];
            });
            dataCourseID = listCoursesChecked;
            return listCoursesChecked;
        }

        const handleData = selector => {
            $body.delegate(selector, 'click', function(e) {
                selectClass.show();
                $('.pagination__wrapper').show();
                semesterChanged = '';
                const { uid, type } = getData(this);
                idProgram = uid;
                handleContentProgram();
                switch (type) {
                    case 'program':
                        $infoWrapper.hide();
                        $('.js-list-course').show();
                        const isChecked = $switchRole.prop('checked');
                        if (uid.length) {
                            resetData();
                            prevIdProgram = uid;
                            if (isChecked === false) {
                                renderCourses(
                                    getCoursesOfTrainer(
                                        getCourseProgramsChecked()
                                    )
                                );
                            } else {
                                renderCourses(getCourseProgramsChecked());
                            }
                        }
                        localStorage.clear('courseId');
                        localStorage.setItem('idProgram', `${listIdChecked}`);
                        break;
                    case 'course':
                        $input.val($(this).text());
                        handleListSearchCourse(uid);
                        break;
                }
                initPagination(getCourseProgramsChecked(), PER_PAGE);
            });
        };

        $('.contentPage').on('click', '.js-read-more', function(e) {
            e.preventDefault();
            var courseId = $(this).data('courseid');
            const course = getDataById(
                courseId,
                'courseId',
                listCoursesOfPhase
            );
            renderInfo(course);
            $('.js-list-course').hide();
            localStorage.setItem('courseId', `${courseId}`);
        });

        function formatSelectSemester(dataFormat) {
            let arr = [];
            arr.push({
                value: 'all',
                label: 'Full Program',
                selected: true,
            });
            dataFormat.forEach(e => {
                let obj = {
                    value: e.semester,
                    label: e.semester,
                    name: 'semester',
                };
                arr.push(obj);
            });
            return arr;
        }
        function setOptionSelect(configOption) {
            if (selectSemester.length) {
                selection.clearChoices();
                selection.setChoices(configOption, 'value', 'label', false);
            }
        }

        let getSemesterId = (listData, semester) => {
            return listData.filter(e => {
                return e.semester === semester;
            });
        };
        let handleSemesterSelect = () => {
            selectSemester.change(event => {
                let selected = event.target.value;
                semesterChanged = selected;
                let arrCoursesTypeSemester = [];
                let arrCoursesOfPhase = getCoursesOfPhase(dataPhaseGeted);
                if (selected === 'all') {
                    if (listIdChecked.length) {
                        arrCoursesTypeSemester = getCourseProgramsChecked();
                    } else {
                        arrCoursesTypeSemester = arrCoursesOfPhase;
                    }
                } else {
                    if (listIdChecked.length) {
                        arrCoursesTypeSemester = getSemesterId(
                            getCourseProgramsChecked(),
                            selected
                        );
                    } else {
                        arrCoursesTypeSemester = getSemesterId(
                            arrCoursesOfPhase,
                            selected
                        );
                    }
                }
                listCoursesSemesterFilter = arrCoursesTypeSemester;
                //reset pagnumber correct with total course of semester
                initPagination(listCoursesSemesterFilter, PER_PAGE);
            });
        };
        handleSemesterSelect();

        //Search Participants Name.
        function searchParticipants() {
            let listParticipant = dataCourse.participants;
            $('#search__participants__input').keyup(function() {
                let value = $(this)
                    .val()
                    .toLowerCase();
                let dataFilterName = listParticipant.filter(e => {
                    return e.fullName.toLowerCase().includes(value);
                });
                if (value === '') {
                    renderParticipants(listParticipant);
                } else {
                    renderParticipants(dataFilterName);
                }
            });
        }

        function orderStatus(column, type) {
            let order = $('.table thead tr>th:eq(' + column + ')').data(
                'order'
            );
            order = order === 'ASC' ? 'DESC' : 'ASC';
            $('.table thead tr>th:eq(' + column + ')').data('order', order);

            //Sort the table
            $('.table tbody tr')
                .sort(function(a, b) {
                    a = $(a)
                        .find('td:eq(' + column + ')')
                        .text();
                    b = $(b)
                        .find('td:eq(' + column + ')')
                        .text();

                    switch (type) {
                        case 'text':
                            let splitA = a.trim().split(' ');
                            let splitB = b.trim().split(' ');
                            let lastA = splitA[splitA.length - 1];
                            let lastB = splitB[splitB.length - 1];
                            if (lastA < lastB) return -1;
                            if (lastA > lastB) return 1;
                            break;
                        case 'number':
                            return order === 'ASC' ? a - b : b - a;
                            break;
                    }
                })
                .appendTo('.table tbody');
        }

        function orderDashInStatus() {
            $('.table tbody tr')
                .sort(function(a, b) {
                    let a1 = $(a)
                        .find('td')
                        .eq(1)
                        .text()
                        .trim();
                    let a2 = $(a)
                        .find('td')
                        .eq(2)
                        .text()
                        .trim();
                    let a3 = $(a)
                        .find('td')
                        .eq(3)
                        .text()
                        .trim();
                    let a4 = $(a)
                        .find('td')
                        .eq(4)
                        .text()
                        .trim();

                    let b1 = $(b)
                        .find('td')
                        .eq(1)
                        .text()
                        .trim();
                    let b2 = $(b)
                        .find('td')
                        .eq(2)
                        .text()
                        .trim();
                    let b3 = $(b)
                        .find('td')
                        .eq(3)
                        .text()
                        .trim();
                    let b4 = $(b)
                        .find('td')
                        .eq(4)
                        .text()
                        .trim();

                    if (
                        (a1 === '-' && b1 !== '-') ||
                        (a2 === '-' && b2 !== '-') ||
                        (a3 === '-' && b3 !== '-') ||
                        (a4 === '-' && b4 !== '-')
                    ) {
                        return 1;
                    } else if (
                        (b1 === '-' && a1 !== '-') ||
                        (b2 === '-' && a2 !== '-') ||
                        (b3 === '-' && a3 !== '-') ||
                        (b4 === '-' && a4 !== '-')
                    ) {
                        return -1;
                    }
                })
                .appendTo('.table tbody');
        }
        const initCourseStatus = () => {
            const queryParam = getParam();
            if ($timeLine.length) {
                $timeLine
                    .yearSlider({ listYears: dataYears })
                    .on('year:redirect', function(event, year) {
                        window.EKINO_ACADEMY.common.redirectURL({ year });
                    });
            }

            // if have param on URL default render data program and course
            if (queryParam) {
                //get uuid from param on URL
                const { phase, dataCourseID } = queryParam;
                $btnPhases
                    .filter((i, e) => e.dataset.id === phase)[0]
                    .classList.add('active');
                initProgram(phase);
                // renderCourses(dataCourseID);
            }
            handlePhases();
            handleSwitch();
            handleSearchClick();
            handleData(item);
            handleUnFocus();
        };

        initCourseStatus();
        $evnReadMore.on('click', function() {
            $loading.show();
        });
        $(document).ready(function() {
            $loading.hide();
        });
    });
}, 'courseStatus');