import $ from 'jquery';
import { loadJSWithPage } from '../utils/helpers'

export default loadJSWithPage(() => {
    $(function () {
        const { yearsData, phases, dataObj, dateSemesters, ojtLink } = window.EKINO_ACADEMY.page.programStatus

    let currentPhaseObj;
    let currentIdPhase;
    let getDataPhaseDefault;
    let currentProgramObj;
    let heightTableDesk = 1;
    const $body = $('body');
    let elmParticipants = $('.participant-session');
    let elmOnTraining = $('.onJobTraining-session');
    let elmTotalCourse = $('.total-course');
    let elmTotalHours = $('.total-hours');
    let elmCompletedCourse = $('.completed-course');
    let elmProgressCourse = $('.progress-course');
    let elmOpenCourse = $('.open-course');
    let elmSeeMore = $('.see-more-participants');
    let elmSeeLess = $('.see-less-participants');
    let phaseTab = '.js-item-phase-program-status';
    let elmLinkCourse = $('.course-url');
    let listFilterDataType = null;
    const $loading = $("#loading");
    const $linkCheckHere = $('.check-here');
    const listStatusModelSorting = ['On-going', 'Pending', 'Finished', 'Cancelled'];
    const listEnrollModelSorting = ['Full Program', 'Semester 1', 'Semester 2'];
    const arrTypeModelSorting= ['team', 'profile', 'semester', 'status'];

    $(document).ready(function () {
        $loading.hide();
    });
    $linkCheckHere.on('click', function () {
        $loading.show();
    })


    const $timeLine = $('#program-status-slider');
    if ($timeLine.length) {
        $timeLine
            .yearSlider({
                listYears: yearsData
            })
            .on('year:redirect', function (event, year) {
                window.EKINO_ACADEMY.common.redirectURL({
                    year
                });
            });

    }

    const resetPrograms = () => {
        let elmProgramList = $('.filter-program-status');
        elmProgramList.html('');
        if (elmProgramList.hasClass('wrapper-filter--center')) {
            elmProgramList.removeClass('wrapper-filter--center');
        }
    }

    const resetOnJobTraining = () => {
        elmOnTraining.addClass('hidden');
        $('.section__onJobTraining').html('');
    }

    const resetCourse = () => {
        let emlCourse = $('.course-session');
        emlCourse.addClass('hidden');
        elmTotalCourse.html('');
        elmTotalHours.html('');
        elmCompletedCourse.html('');
        elmProgressCourse.html('');
        elmOpenCourse.html('');
    }

    const resetParticipantsDesk = () => {
        elmParticipants.addClass('hidden');
        elmSeeLess.addClass('hidden');
        elmSeeMore.addClass('hidden');

        $('.scroll-table-desk').html('');
    }

    const resetParticipantsMobile = () => {
        $('.scroll-table-mobile').html('');
    }

    const resetDataPage = () => {
        resetParticipantsDesk();
        resetParticipantsMobile();
        resetOnJobTraining();
        resetCourse();
    }
    const getPhaseById = (idPhase) => {
        for (var i = 0; i < dataObj.phases.length; i++) {
            if (idPhase === dataObj.phases[i].id) {
                return dataObj.phases[i];
            }
        }
    }

    const getProgramById = (currentPhaseObj, idProgram) => {
        for (var i = 0; i < currentPhaseObj.programs.length; i++) {
            if (idProgram === currentPhaseObj.programs[i].id) {
                return currentPhaseObj.programs[i];
            }
        }
    }

    const initPrograms = (currentPhaseObj) => {
        let tmpPrograms = '';
        for (let i = 0; i < currentPhaseObj.programs.length; i++) {
            let item = currentPhaseObj.programs[i];
            let arrSem1 = [];
            let arrSem2 = [];
            let sem1;
            let sem2;
            dateSemesters.forEach( e => {
                if(e.programId === item.id){
                    e.semesterDates.forEach(elm => {
                        if(elm.includes('Semester 2')){
                         arrSem2.push(elm)
                        }else{
                         arrSem1.push(elm)
                        }
                     });
                     sem1 = arrSem1.toString().split(';');
                     sem2 = arrSem2.toString().split(';');
                }
            })
            tmpPrograms += `<label class="wrapper-item">
                                <input type="radio" name="checkProgram" value="${item.id}" class="js-check-program">
                                <span class="checkmark"></span>
                                <div class="content">
                                    <p class="content__title">${item.name}</p>
                                    <p class="content__time"><span>${sem1[0]}: </span>${sem1[1]} - ${sem1[2]}</p>
                                    <p class="content__time"><span>${sem2[0]}: </span>${sem2[1]} - ${sem2[2]}</p>
                                </div>
                            </label>`;
        }
        $('.filter-program-status').append(tmpPrograms);
        if (currentPhaseObj.programs.length === 1) {
            $('.filter-program-status').addClass('wrapper-filter--center');
        }
    }

    const getUrlCourse = (dataCurrentProgram) => {
        const queryParams = [];
        queryParams.push('year=' + dataObj.year);
        if(!currentIdPhase){
            queryParams.push('phase=' + getDataPhaseDefault.id);
        }else{
            queryParams.push('phase=' + currentPhaseObj.id);
        }
        queryParams.push('program=' + dataCurrentProgram.id);

        const queryString = queryParams.join("&");
        const path = window.location.href.split(/[?#]/)[0];
        return path + '/course-status?' + queryString;
    }

    const initCourse = (dataCurrentProgram) => {
        let objCourse = dataCurrentProgram.courses;
        let elmCourse = $('.course-session');
        let urlCourseDetail = '';
        if (elmCourse.hasClass('hidden')) {
            elmCourse.removeClass('hidden');
            elmTotalCourse.html(objCourse.totalCourse);
            elmTotalHours.html(objCourse.totalHours);
            elmCompletedCourse.html(objCourse.completed);
            elmProgressCourse.html(objCourse.inProgress);
            elmOpenCourse.html(objCourse.open);
        }
        urlCourseDetail = getUrlCourse(dataCurrentProgram);
        elmLinkCourse.attr('href', urlCourseDetail);
    }

    const initOnJobTraining = (currentProgramObj) => {
        let tmpTraining = '';
        let elmTraining = $('.onJobTraining-session');
        if (elmTraining.hasClass('hidden')) {
            elmTraining.removeClass('hidden');
        }
        const pathLocal = window.location.href.split(/\/program-status/ig)[0];
        const path = pathLocal + ojtLink + '/';
        const queryParams = [];
        queryParams.push()
        for (var i = 0; i < currentProgramObj.onJobTraining.length; i++) {
            let item = currentProgramObj.onJobTraining[i];
            tmpTraining += `<div class="onJobTraining-item">
                                <div class="wrapper-groups">
                                    <div class="info-group">
                                        <p class="group__label">Project name&#58;</p>
                                        <p class="group__value">${item.projectName}</p>
                                    </div>

                                    <div class="info-group">
                                        <p class="group__label">Participants&#58;</p>
                                        <p class="group__value">${item.participants}</p>
                                    </div>

                                    <div class="info-group">
                                        <p class="group__label">Project manager&#58;</p>
                                        <p class="group__value">${item.projectLead}</p>
                                    </div>

                                    <div class="info-group">
                                        <p class="group__label">Start date&#58;</p>
                                        <p class="group__value">${item.startDate}</p>
                                    </div>

                                    <div class="info-group">
                                        <p class="group__label">Project status&#58;</p>
                                        <p class="group__value">${item.projectStatus}</p>
                                    </div>

                                    <div class="info-group">
                                        <p class="group__label">End date&#58;</p>
                                        <p class="group__value">${item.endDate}</p>
                                    </div>
                                </div>
                                <div class="section__btn">
                                    <a class="evn-btn js-details-btn" href="${path + item.uriName}">Details</a>
                                </div>
                            </div>`;
        }
        $('.section__onJobTraining').append(tmpTraining);
        if(ojtLink === '#'){
            $( ".js-details-btn" ).wrap( "<div class='add-cursor'></div>" );
            $(".js-details-btn").attr('disabled', true)
        }
    }

    const initParticipants = (listParticipants, selectElm) => {
        let tmpParticipants = '';
        for (var i = 0; i < listParticipants.length; i++) {
            const pathLocal = window.location.href.split(/[?#]/)[0];
            const pathChange = '/participant-status';
            const path = pathLocal.concat(pathChange);
            let item = listParticipants[i];
            tmpParticipants += `<tr>
                                        <td data-label="No">${i + 1}</td>
                                        <td data-label="Name">${item.name}</td>
                                        <td data-label="Department">${item.team}</td>
                                        <td data-label="Profile">${item.profile}</td>
                                        <td data-label="Enroll Type">${item.semester}</td>
                                        <td data-label="Status" class="check-here">
                                            <a class="link-check-here check-here-img" href="${path + item.link}">
                                                ${item.status}
                                            </a>
                                        </td>
                                    </tr>`;
        }
        $(`.${selectElm}`).html(tmpParticipants);

    }
    const initInfoSemester = (listdataSem) => {
        let tmpParticipants = '';
        for (var i = 0; i < listdataSem.length; i++) {
            let item = listdataSem[i];
            tmpParticipants += `<div class="section__content section__course">
            <div class="evn-row-name"> Semester ${i + 1}</div>
            <div class="wrapper-info-semester">
                <div class="info-course">
                    <div class="info-course__label">Total courses</div>
                    <div class="info-course__value total-course">${item.totalCourse}</div>
                </div>
                <div class="info-course">
                    <div class="info-course__label">Total hours</div>
                    <div class="info-course__value total-hours">${item.totalHours}</div>
                </div>
                <div class="info-course">
                    <div class="info-course__label">Completed</div>
                    <div class="info-course__value completed-course">${item.completed}</div>
                </div>
                <div class="info-course">
                    <div class="info-course__label">In progress</div>
                    <div class="info-course__value progress-course">${item.inProgress}</div>
                </div>
                <div class="info-course">
                    <div class="info-course__label">To-do</div>
                    <div class="info-course__value open-course">${item.todo}</div>
                </div>
            </div>
        </div>`;
        }
        $(`.wrap-info-course`).html(tmpParticipants);
    }

    const initParticipantsDesk = (currentProgramObj) => {
        if (elmParticipants.hasClass('hidden')) {
            elmParticipants.removeClass('hidden');
        }
        $('.total-participants').html(currentProgramObj.participants.length + ' total participants');
        initParticipants(currentProgramObj.participants, 'scroll-table-desk');
        searchParticipantsName(currentProgramObj , 'scroll-table-desk');
        filterContentTable();
        filterColumnLikeExcel(currentProgramObj , 'scroll-table-desk');
    }

    const initParticipantsMobile = (currentProgramObj) => {
        if (elmParticipants.hasClass('hidden')) {
            elmParticipants.removeClass('hidden');
        }
        initParticipants(currentProgramObj.participants, 'scroll-table-mobile');
        searchParticipantsName(currentProgramObj, 'scroll-table-mobile');
    }

    const initDataPage = (currentProgramObj) => {
        initParticipantsDesk(currentProgramObj);
        initParticipantsMobile(currentProgramObj);
        getListFilterDataType(currentProgramObj);
        setHeightTableDesk();
        initCourse(currentProgramObj);
        initOnJobTraining(currentProgramObj);
        initInfoSemester(currentProgramObj.courses);
    }

    const setHeightTableDesk = () => {
        const elmTable = $('#participant-table');
        const rowTable = $('.scroll-table-desk > tr');
        if (heightTableDesk > 1) {
            heightTableDesk = 1;
        }
        heightTableDesk += $('.table-head-desk')[0].clientHeight;
        rowTable.each(function (index) {
            if (index < 5) {
                heightTableDesk += $(this)[0].clientHeight;
            }
        });
        if(rowTable.length <= 5) {
            elmSeeMore.addClass('hidden');
            elmSeeLess.addClass('hidden');
            elmTable.css('overflow-y', 'visible');
        } else {
            elmSeeMore.removeClass('hidden');
            elmSeeLess.addClass('hidden');
            elmTable.css('overflow-y', 'auto');
        }
        $('.table-desktop').css('height', heightTableDesk + 'px');
    }

    $body.delegate('input[name="checkProgram"]', 'click', event => {
        let idProgram = event.target.value;
        if(!currentIdPhase){
            currentProgramObj = getProgramById(getDataPhaseDefault, idProgram);
        }else{
            currentProgramObj = getProgramById(currentPhaseObj, idProgram);
        }
        $('.select-all').prop('checked', true);
        $('#search-participants-input').val('');
        resetDataPage();
        initDataPage(currentProgramObj);        
    });

    function getListFilterDataType(currentProgramObj) {
        const participants = currentProgramObj.participants;
        listFilterDataType = {
            team: filterListOption(participants, 'team'),
            profile: filterListOption(participants, 'profile'),
            semester: filterListOption(participants, 'semester'),
            status: filterListOption(participants, 'status'),
        }
    }

    const handleDefaultDisplay = () => {
        let dataPhaseDefault = dataObj.phases[0];
        getDataPhaseDefault = dataPhaseDefault;
        $('.js-item-phase-program-status:first-child').addClass('active');
        initPrograms(dataPhaseDefault);
        $('input[type="radio"]').eq(0).attr("checked", true);
        initDataPage(dataPhaseDefault.programs[0]);
    }
    handleDefaultDisplay();

    const isExistPhase = (idPhase) => {
        for (var i = 0; i < dataObj.phases.length; i++) {
            if (idPhase === dataObj.phases[i].id) {
                return true;
            }
        }
        return false;
    }

    elmSeeMore.click(function () {
        $(this).addClass('hidden');
        elmSeeLess.removeClass('hidden');
        let heightTable = $('.table-desktop > .table-content')[0].clientHeight + 1;
        $('.table-desktop').css('height', heightTable + 'px');
    })

    elmSeeLess.click(function () {
        $(this).addClass('hidden');
        elmSeeMore.removeClass('hidden');
        $('.table-desktop').css('height', heightTableDesk + 'px');
    })

    $(window).resize(function (e) {
        e.stopPropagation();
        if (window.innerWidth > 768) {
            if (!elmSeeMore.hasClass('hidden') && elmSeeMore.length) {
                setHeightTableDesk();
            }
        }
    })

    const disablePhase = () => {
        $('.wrapper-item > input[name="checkPhase"]').each(function (index) {
            if (!isExistPhase($(this)[0].value)) {
                $(this)[0].disabled = true;
            }
        })
    }

    const disabledPhaseTab = () => {
        phases.map(phase => {
            if (phase.disabled) {
                $(`${phaseTab}[data-value=${phase.id}]`).addClass('disabled');
            }
        })
    }

    const resetActivePhase = () => {
        $(phaseTab).each(function () {
            if ($(this).hasClass('active')) {
                $(this).removeClass('active');
            }
        })
    }

    $(phaseTab).click(function () {
        const phaseID = $(this).data('value');
        currentIdPhase = phaseID;
        const phase = phases.find(phase => phase.id === phaseID);
        $('.select-all').prop('checked', true);
        if (!phase.disabled) {
            resetActivePhase();
            $(this).addClass('active');
            currentPhaseObj = getPhaseById(phaseID);
            resetPrograms();
            resetDataPage();
            initPrograms(currentPhaseObj);        
            currentProgramObj = currentPhaseObj.programs[0];
            initDataPage(currentProgramObj);
            $('input[type="radio"]').eq(0).attr("checked", true);

        }
    })

    const initiation = () => {
        disablePhase();
        disabledPhaseTab();
    }

    initiation();
    function searchParticipantsName(currentProgramObj, element) {
        let dataTable = currentProgramObj.participants;
        $('.search__participants__table').on('keyup', function () {
            let value = $(this).val().toLowerCase();
            let dataFilterName = dataTable.filter( item => {
                return item.name.toLowerCase().includes(value);
            })
            if(value === ""){
                initParticipants(dataTable, element)
            }else {
                initParticipants(dataFilterName, element);
            }
            setHeightTableDesk();
        });
    }

    function filterListTypeStatus(listData, modelOrderStatus, modelOrderEnroll) {
        return listData.sort((a, b) => {
            if (modelOrderStatus.indexOf(a.status) > modelOrderStatus.indexOf(b.status)) return 1;
            if (modelOrderStatus.indexOf(a.status) < modelOrderStatus.indexOf(b.status)) return -1;
            if (modelOrderEnroll.indexOf(a.semester) > modelOrderEnroll.indexOf(b.semester)) return 1;
            if (modelOrderEnroll.indexOf(a.semester) < modelOrderEnroll.indexOf(b.semester)) return -1;
            if(a.team > b.team) return 1;
            if(a.team < b.team) return -1;
            if(a.name.split(" ")[(a.name.split(" ")).length - 1] > b.name.split(" ")[(b.name.split(" ")).length - 1]) return 1;
            if(a.name.split(" ")[(a.name.split(" ")).length - 1] < b.name.split(" ")[(b.name.split(" ")).length - 1]) return -1;
            return 0;
        })
    }

    $body.on('click', '.dropdown-filter-content', (e) => {
        e.stopPropagation();
      });

    function filterContentTable () {
            $('.js-arrow-filter').off('click').on('click', function(e) {
                e.stopPropagation();
                const $self = $(e.currentTarget);
                const $parent = $self.parent();
                if($parent.hasClass('active')){
                    $parent.removeClass('active');
                }else{
                    $('.participant-col').removeClass('active');
                    $parent.addClass('active');
                }
            });
       $(document).on('click', function(e) {
        if(!e.target.className.includes('.dropdown-filter-content')
        ) {
                $('.participant-col').removeClass('active');
            }
       });
    }

    function renderListOption(dataItem, dataType) {
        const $columnType =  $(`[data-type="${dataType}"]`);
        let tmpOption = '';
        for (let i = 0; i < dataItem.length; i++) {
            let item = dataItem[i];
            tmpOption += `<div class="dropdown-filter-item">
            <input type="checkbox" value="${item}" name="filter-col" checked="checked" class="dropdown-filter-menu-item item js-item-checkbox">
            ${item}
        </div>`;
        }
        $columnType.find('.js-option-item').html(tmpOption);
    }

    function filterListOption (data,type) {
        let arrOptions = [];
        data.forEach(e => {
            if(!arrOptions.includes(e[`${type}`])){
                arrOptions.push(e[`${type}`]);
            }
        })
        let dataListOption = arrOptions.sort();
        return dataListOption;
    }

    const getValueOptionChecked = (dataType) => {
        let $typeColumn =  $(`[data-type="${dataType}"]`);
        const listChecked = $typeColumn.find("input[name='filter-col']:checked");
        let arrValueChecked = [];
        listChecked.each(function(){
            arrValueChecked.push($(this).attr('value'));
        });
        return arrValueChecked;
    }

    function filterWithCheckboxByDataType(data, dataType, listCheckbox) {
        return data.filter( e => listCheckbox.includes(e[dataType]));
    }

    function filterWithAllCheckbox(data, filterList) {
        let arrTemp = data;
        Object.entries(filterList).forEach(([key, value]) => {
            if(arrTemp.length === 0) {
                return false;
            }
            arrTemp = filterWithCheckboxByDataType(arrTemp, key, value);
        });
        return arrTemp;
    }

    function filterColumnLikeExcel (currentProgramObj, element) {
        let dataTable = currentProgramObj.participants;
        const listOrderedStatus = filterListTypeStatus(dataTable, listStatusModelSorting, listEnrollModelSorting);
        initParticipants(listOrderedStatus,element);
        arrTypeModelSorting.forEach(item => {
                renderListOption(filterListOption(listOrderedStatus, item), item)
        });

        $('.select-all').change(function(e){
            const $itemCheckbox = $(this).parent('.js-select-all').siblings('.js-option-item').children('.dropdown-filter-item').children('.js-item-checkbox');
            $itemCheckbox.prop('checked', $(this).prop('checked'));
        });

            $('.dropdown-filter-menu-item').on('change', function(e) {
                const $self = $(e.currentTarget);
                const $selectedItem = $(this).closest('.js-wrapper-option').find('.js-item-checkbox');
                const $selectedItemChecked = $(this).closest('.js-wrapper-option').find('.js-item-checkbox:checked');
                const $selectedAll = $(this).closest('.js-wrapper-option').children('.js-select-all').children('.select-all');
                    if($(this).prop('checked') === false){
                        $selectedAll.prop('checked', false);
                    }
                    if($selectedItemChecked.length === $selectedItem.length){
                        $selectedAll.prop('checked', true);
                    }
                const dataType = $self.parents('.participant-col').data('type');
                let listValueOption = getValueOptionChecked(dataType);
                listFilterDataType[dataType] = listValueOption;
                const filteredData = filterWithAllCheckbox(listOrderedStatus, listFilterDataType);
                initParticipants(filteredData,element);
                setHeightTableDesk();
            });
    }
});

}, "programStatus");