export const storeName  = "EKINO_ACADEMY"

let initiatedStore = false
export async function setPageData(page, mockData) {
  console.log(page, "mockData", mockData)
  if(!window[storeName]) {
    console.log("Store has not initiated yet. Please use window.EKINO_ACADEMY.initStore")
    return
  }

  window[storeName].page = {
    ...window[storeName].page,
    [page]: mockData
  }

  return Promise.resolve(window[storeName])

}

export async function getPageData(page) {
  if(!window[storeName]) {
    console.log("Store has not initiated yet")
    return
  }

  console.log(window[storeName].page)

  return Promise.resolve(window[storeName].page[page]|| {})
}

function initStore() {
  if(initiatedStore) {
    console.log("Store is initiated. Please use window.EKINO_ACADEMY.setPageData OR window.EKINO_ACADEMY.getPageData")
    return
  }

  window[storeName] = window[storeName] || { page: {} }
  window[storeName].setPageData = setPageData
  window[storeName].getPageData = getPageData
  initiatedStore = true;    
  return Promise.resolve(window[storeName])
}

export async function startWithMock(mocks, cb) {
  if(mocks) {
    for(let i=0; i < mocks.length; i++) {
      const key = Object.keys(mocks[i])[0]
      await setPageData(key, mocks[i][key])
    }

    console.log(mocks, "mocks")

    if(cb) {
      cb(window[storeName].page)
    }
  }
}

export function addMockToGlobalState(data) {  
  window[storeName].page = data
}

initStore()