import $ from 'jquery';
import PhotoSwipe from 'photoswipe';
import PhotoSwipeUI_Default from 'photoswipe/dist/photoswipe-ui-default';

import { loadJSWithPage } from '../utils/helpers'

export default loadJSWithPage(() => {
    const pswpTemplate = `
    <div class="pswp" tabindex="-1" role="dialog" aria-hidden="true">
        <div class="pswp__bg"></div>
        <div class="pswp__scroll-wrap">
            <div class="pswp__container">
                <div class="pswp__item"></div>
                <div class="pswp__item"></div>
                <div class="pswp__item"></div>
            </div>
            <div class="pswp__ui pswp__ui--hidden">
                <div class="pswp__top-bar">
                    <div class="pswp__counter"></div>
                    <button
                        class="pswp__button pswp__button--close"
                        title="Close (Esc)"
                    ></button>

                    <button
                        class="pswp__button pswp__button--zoom"
                        title="Zoom in/out"
                    ></button>
                    <div class="pswp__preloader">
                        <div class="pswp__preloader__icn">
                            <div class="pswp__preloader__cut">
                                <div class="pswp__preloader__donut"></div>
                            </div>
                        </div>
                    </div>
                </div>
                <div
                    class="pswp__share-modal pswp__share-modal--hidden pswp__single-tap"
                >
                    <div class="pswp__share-tooltip"></div>
                </div>
                <button
                    class="pswp__button pswp__button--arrow--left"
                    title="Previous (arrow left)"
                ></button>
                <button
                    class="pswp__button pswp__button--arrow--right"
                    title="Next (arrow right)"
                ></button>
                <div class="pswp__caption">
                    <div class="pswp__caption__center"></div>
                </div>
            </div>
        </div>
    </div>
    `;
    const $loading = $("#loading");
    $(document).ready(function () {
        $loading.hide();
    });

    $(function () {
        const $window = $(window);
        const isMobile = $window.width() <= 800 || $window.height() <= 600;

        if (isMobile) {
            initializeZoomablePictures();
        }

        function initializeZoomablePictures() {
            $(document.body).append(pswpTemplate);

            $('.zoomable-picture').click(function (event) {
                event.preventDefault();
                const $link = $(this);
                const pswpWrapper = $('.pswp').get(0);

                const pswpOptions = {
                    index: 0,
                    bgOpacity: 0.85,
                    showHideOpacity: true,
                };

                const items = [{
                    src: $link.attr('href'),
                    title: $link.data('caption'),
                    w: $link.data('width'),
                    h: $link.data('height'),
                }, ];

                new PhotoSwipe(
                    pswpWrapper,
                    PhotoSwipeUI_Default,
                    items,
                    pswpOptions
                ).init();
            });
        }
    });
}, "certificate");