import $ from 'jquery';
import { loadJSWithPage } from '../utils/helpers'

const tabActive = () => {    
    const $tabLabel = $('.js-program-tab-name');
    const $tabContent = $('.js-program-tab-content-item');

    const initTabActive = () => {
        if ($tabLabel.length) {
            $tabLabel.eq(0).addClass('active');
        }

        if ($tabContent.length) {
            $tabContent.eq(0).addClass('show');
        }
    };

    $tabLabel.click(e => {
        const $target = $(e.target);
        const currentIDTarget = $target.data('content-id');
        $tabContent.removeClass('show');
        $tabLabel.removeClass('active');
        $target.addClass('active');
        $(`#${currentIDTarget}`).addClass('show');
    });

    initTabActive();
};

export default loadJSWithPage(() => {
    $(function() {
        const { yearsData, courseList = [] } = window.EKINO_ACADEMY.page.participantProgress
        tabActive();

        const $evnBtn = $('.evn-btn');
        $evnBtn.on('click', function () {
            $loading.show();
        });
        // semsterActive();
        const $loading = $("#loading");
        $(document).ready(function () {
            $loading.hide();
        });
        const $timeLine = $('#participant-progress-slider');
    if ($timeLine.length) {
        $timeLine
            .yearSlider({
                listYears: yearsData
            })
            .on('year:redirect', function (event, year) {
                window.EKINO_ACADEMY.common.redirectURL({
                    year
                });
            });
    }

    // render semester
    function renderSemester(courseList) {
        let tmpContent = renderContent(courseList);
        const tmpSemester = `<div class="content-semester">
                                <table class="table-content table show-table-one">
                                    <thead class="js-table-header">
                                        <tr>
                                            <th>#</th>
                                            <th>Course name</th>
                                            <th>Quiz</th>
                                            <th>Homework</th>
                                            <th>Rate</th>
                                            <th>Score</th>
                                        </tr>
                                    </thead>
                                    <tbody class="scroll-table">
                                        ${tmpContent}
                                    </tbody>
                                </table>
                            </div>`;
        $('.js-content-semester').append(tmpSemester);
    }

    //render content for semester
    function renderContent(courseList) {
        let tmpContent = '';

        for (let idxCourseList = 0; idxCourseList < courseList.length; idxCourseList++) {
            let course = courseList[idxCourseList];
            let numberOfQuizzes = course.numberOfQuizzes;
            if(!numberOfQuizzes) {
                numberOfQuizzes = 1;
            }
            const elmOfQuizzes = course.quizzes;

            let tmpQuiz = '';
            let tmpRate = '';
            for(let idxNumQuiz = 0; idxNumQuiz < numberOfQuizzes; idxNumQuiz++) {
                let quiz = elmOfQuizzes.filter((item) => {
                    let newIdxNumQuiz = String(idxNumQuiz + 1);
                    return item.quizNo === newIdxNumQuiz;
                });
                if (quiz.length) {
                    tmpQuiz += `<div class="quizzes__score">${quiz[0].quizScore}</div>`
                } else {
                    tmpQuiz += `<div class="quizzes__score">-</div>`
                }
            }
            for(let idxNumRate = 0; idxNumRate < numberOfQuizzes; idxNumRate++) {
                let rate = elmOfQuizzes.filter((item) => {
                    let newIdxNumRate = String(idxNumRate + 1);
                    return item.quizNo === newIdxNumRate;
                });
                if (rate.length && rate[0].conditionalRate) {
                    tmpRate += `<div class="quizzes__score">${rate[0].conditionalRate}</div>`
                } else {
                    tmpRate += `<div class="quizzes__score">-</div>`
                }
            }

            tmpContent += `<tr>
                            <td data-label="#">
                                ${idxCourseList + 1}
                            <td data-label="Name">
                                ${course.course.nodeName||"-"}
                            </td>
                            <td data-label="Quiz" class="quizzes">
                                <div class="quizzes__content">
                                    ${tmpQuiz}
                                </div>
                            </td>
                            <td
                                data-label="Homework">
                                ${course.homework||"-"}
                            </td>
                            <td data-label="Rate" class="quizzes" id="quizzes">
                                <div class="quizzes__content">
                                    ${tmpRate}
                                </div>
                            </td>
                            <td data-label="Score" class="bold-score">
                                ${course.score||"-"}
                            </td>
                        </tr>`;

        }
        return tmpContent;
    }
    function searchCourse() {
        $(document).ready(function () {
            $('#program-tab-name-search-input').keyup(function () {
                const searchField = $(this).val();
                const regex = new RegExp(searchField, 'i');

                const textOption = $('#dropdown-semester option:selected').val();
                $('.dropdown-value-project').html(textOption);

                let dataShow = courseList.filter(item => {
                    return item.course.nodeName.match(regex);
                });

                let dataSearch = [];
                if(textOption === 'all') {
                    dataSearch = dataShow;
                    $('.js-content-semester').html('');
                    renderSemester(dataSearch);
                } else {
                    dataSearch = dataShow.filter((course)=>{
                        return course.semester === textOption;
                    })
                    $('.js-content-semester').html('');
                    renderSemester(dataSearch);
                }
                if(!this.value){
                    $('.js-content-semester').html('');
                    renderSemester(dataSearch);
                }
            });
        })
    }

    function nameProgramActive () {
        const $nameSemster = $('#dropdown-semester');

        $nameSemster.change(() => {
            $('.js-content-semester').html('');
            const textOption = $('#dropdown-semester option:selected').val();
            $('.dropdown-value-project').html(textOption);

            if (textOption) {
                $('#program-tab-name-search-input').val('');
            }

            // find course of semester
            let data = [];
            if (textOption === 'all') {
                data = courseList;
            } else {
                data = courseList.filter((course) => {
                    return course.semester == textOption;
                });
            }
            renderSemester(data, textOption);
        });
    }

    function sortDateValid() {
        const sortedActivities = courseList.sort((a, b) =>
            (a.updatedDate.date.year - b.updatedDate.date.year) || (a.updatedDate.date.month - b.updatedDate.date.month)
            || (a.updatedDate.date.day - b.updatedDate.date.day) || (a.updatedDate.time.hour - b.updatedDate.time.hour)
            || (a.updatedDate.time.minute - b.updatedDate.time.minute) || (a.updatedDate.time.second - b.updatedDate.time.second)
            || (a.updatedDate.time.nano - b.updatedDate.time.nano))
        return sortedActivities;
    }

    sortDateValid();
    nameProgramActive();
    renderSemester(courseList, 'All');
    searchCourse();

});

}, "participantProgress");
