import $ from 'jquery';
import 'twbs-pagination/jquery.twbsPagination'
import Choices from 'choices.js';
import { loadJSWithPage } from '../utils/helpers'

export default loadJSWithPage(() => {
    $(function () {
        const { categories, courseList, courseDetailUrl } = window.EKINO_ACADEMY.page.courseList
                
        const selection = new Choices('#dropdown-project', {itemSelectText: ''});
        const semesterSelection = new Choices('#select-sem-courseList', {itemSelectText: ''});
        const statusSelection = new Choices('#js-dropdown__status', {itemSelectText: ''});
        const $selectStatus = $('#js-dropdown__status');
        const $selectSemester = $('#select-sem-courseList');
        let valueSemesterSelect = '';
        let valueCategorySelect = '';
        let listCourseFilterByCategory = [];
        let staticStatus = ["quiz", "class", "homework"];
        let coursesFilterBySemester = courseList;
        const $loading = $("#loading");        
        const $evnReadMore = $('.read-more');

        $evnReadMore.on('click', function () {
            $loading.show();
        });
        $(document).ready(function () {
            $loading.hide();
        });

        function allHours(listItem) {
            var sum = 0;
            for (let i = 0; i < listItem.length; i++) {
                sum += (listItem[i].hours);
            }
            return sum;
        }
        
        courseTotal(courseList.length, allHours(courseList))

        function courseTotal(numCourse, numHours) {
            let $num = $(`<p>Total <span class="course__total--bold">${numCourse}</span> courses /
            <span class="course__total--bold">${numHours}</span> hours </p>`)
            $("#number-course").html($num);
        }
        
        $('div.schedule').click(function (e) {
            window.location.href = decodeURI(scheduleIconUrl);
        });

        let trainingUrl = '#url';
        let ojtProjectName = '#training';
        let trainingIconUrl = trainingUrl + "/" + ojtProjectName;
        $('div.training').click(function (e) {
            window.location.href = decodeURI(trainingIconUrl);
        });


        function formatConfigSelect(dataFormat) {
            var arr = [];
            arr.push({
                value: 'all',
                label: 'All categories',
                selected: true
            });
            for (var i = 0; i < dataFormat.length; i++) {
                var obj = {
                    value: dataFormat[i].displayName,
                    label: dataFormat[i].displayName
                };
                arr.push(obj);
            }
            return arr;
        }
                
        $('#dropdown-project').change((event) => {
            let selected = event.target.value;
            valueCategorySelect = selected;
            let allCourseByCategory = [];
            if (selected === 'all') {
                if(!valueSemesterSelect){
                    allCourseByCategory = courseList;
                } else{
                    allCourseByCategory = coursesFilterBySemester;
                }

            } else {
                if(!valueSemesterSelect){
                    allCourseByCategory = getCourseByCategory(selected, courseList)
                }else{
                    allCourseByCategory = getCourseByCategory(selected, coursesFilterBySemester)
                }
            }
            listCourseFilterByCategory = allCourseByCategory;
            handleRenderFinal(allCourseByCategory);
            // set options for status
            let listStatusOption = formatConfigStatusSelect(getStatusCourses(allCourseByCategory));
            setStatusOption(listStatusOption);
        })

        function getCourseByCategory(typeValue, dataCourses) {
            let arrCourseFilterByCategory = [];
            arrCourseFilterByCategory = dataCourses.filter( e => e.courseDetail.category.displayName === typeValue);
            return arrCourseFilterByCategory;
        }

        const formatSelectSemester = (dataFormat) => {
            let arrOptions = [];
            let mapArrOption = [];
            arrOptions.push({
                value: 'all',
                label: 'Full Program',
                selected: true
            });
            dataFormat.forEach(e => {
                mapArrOption = arrOptions.map(e => e.value);
                if(!mapArrOption.includes(e.semester)){
                    let obj = {
                        value: e.semester,
                        label: e.semester
                    };
                    arrOptions.push(obj);
                }
            });
            return arrOptions;
        };

        function renderCourse(listShowOnePage, courseDetailUrl, startIndex) {
            var tmpContent = '';
            for (var i = 0; i < listShowOnePage.length; i++) {
                var course = listShowOnePage[i];
                tmpContent += `<div class="project__item">
                                <div class="project-item-wrapper">
                                    <div class="item__title">
                                        <span class="item__number">${startIndex + i + 1}</span>
                                        <span class="item__lable">${course.courseDetail.nodeName}</span>
                                    </div>
                                    <div class="item__content">
                                        <div class="read-more">
                                            <a href="${courseDetailUrl}/${course.readMoreLink}">Read more</a>
                                        </div>
                                        <div class="wrapper-item status-${course.courseStatus.displayName.toLowerCase()}">
                                            <div class="wrapper-progress">
                                                <div class="progress-item">
                                                    <div class="progress progress__open progress--borderLeft">
                                                        <div class="progress-status"></div>
                                                    </div>
                                                    <p class="progress__lable">open</p>
                                                </div>
                                                <div class="progress-item">
                                                    <div class="progress progress__class">
                                                        <div class="progress-status"></div>
                                                    </div>
                                                    <p class="progress__lable">class</p>
                                                </div>
                                                <div class="progress-item">
                                                    <div class="progress progress__quiz">
                                                        <div class="progress-status"></div>
                                                    </div>
                                                    <p class="progress__lable">quiz</p>
                                                </div>
                                                <div class="progress-item">
                                                    <div class="progress progress__homework">
                                                        <div class="progress-status"></div>
                                                    </div>
                                                    <p class="progress__lable">h-work</p>
                                                </div>
                                                <div class="progress-item">
                                                    <div class="progress progress--borderRight progress__finished">
                                                    </div>
                                                    <p class="progress__lable">finished</p>
                                                </div>
                                            </div>
                                            <div class="lever">
                                                <img src="${course.courseDetail.group.icon.link||"#"}" alt="category-icon">
                                                <span>${course.courseDetail.group.displayName||"No Group"}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>`;
            }
            $('.js-course-list').html(tmpContent);
        }

        $selectSemester.change((event) => {
            var selected = event.target.value;
            valueSemesterSelect = selected;
            var listCategories = [];
            var allCourse = [];
            if (selected === 'all') {
                listCategories = categories;
                allCourse = courseList;
            } else {
                let listCourse = getSemesterId(selected);                
                listCategories = getOptionsCategoryBySem(listCourse);
                allCourse = listCourse;                
            }
            coursesFilterBySemester = allCourse;
            handleRenderFinal(allCourse);
            // set options for category
            let optionSelectCategories = formatConfigSelect(listCategories);
            setOptionSelectCategories(optionSelectCategories);
            // set options for status
            let listStatusOption = formatConfigStatusSelect(getStatusCourses(allCourse));
            setStatusOption(listStatusOption);
        });

        function getOptionsCategoryBySem(listCourse) {
            let listCategories = [];
            if (listCourse.length) {
                listCourse.forEach(element => {
                    if (element.courseDetail && element.courseDetail.category) {
                        let checkExist = true;
                        listCategories.forEach(element2 => {
                            if (element2.displayName == element.courseDetail.category.displayName) {
                                checkExist = false;
                            }
                        });
                        if (checkExist) {
                            let item = {
                                displayName: element.courseDetail.category.displayName,
                            };
                            listCategories.push(item);
                        }
                    }
                });
            }
            return listCategories;
        }

        function getSemesterId(idSemester) {
            return courseList.filter((course) => {
                return course.semester === idSemester;
            });
        }

        function getCourseAllPrograms(valueSemesterSelect) {
            return courseList.filter((course) => {
                return course.semester === valueSemesterSelect;
            })
        }

        $('#dropdown-project').change(() => {
            let textOption = $('#dropdown-project option:selected').text();
            $('.dropdown-value-project').html(textOption);
        });

        $('#goToTopPage').click(() => {
            $('html,body').animate({
                scrollTop: 0
            }, 300);
        });

        function getListStatus(listStatus) {
            let arrStatus = [...listStatus];
            staticStatus.forEach((status) => {
                for (var i = 0; i < arrStatus.length; i++) {
                    if (status === arrStatus[i]) {
                        arrStatus.splice(i, 1);
                        i--;
                    }
                }
            });
            if (arrStatus.length !== listStatus.length) {
                arrStatus.push('in progress');
            }
            return arrStatus;
        }

        function getStatusCourses(listCourse){
            let listStatus = [];
            listCourse.forEach( elm => {
                let getNodeName = elm.courseStatus.nodeName.toLowerCase();
                if(elm.courseStatus){
                    if(!listStatus.includes(getNodeName)){
                        listStatus.push(getNodeName)
                    }
                }
            })
            let listStatusFinal = getListStatus(listStatus);
            return listStatusFinal;
        }

        function getCourseByStatus(listCourses, valueStatus) {
            let arrCourse = [];
            if(valueStatus !== 'in progress'){
                arrCourse = listCourses.filter(course => 
                course.courseStatus.nodeName.toLowerCase() === valueStatus)
            }else{
                staticStatus.forEach (status => {
                    for (var i = 0; i < listCourses.length; i++) {
                        if (status === listCourses[i].courseStatus.nodeName.toLowerCase()) {
                            arrCourse.push(listCourses[i]);
                        }
                    }
                })
            }
                return arrCourse;
        }

        function handleStatusSelect() {
            $selectStatus.change(event => {
                let allCoursesByStatus = [];
                let selected = event.target.value;
                if(selected === 'all'){
                    if(!valueSemesterSelect && !valueCategorySelect){
                        allCoursesByStatus = courseList;
                    }else if(valueSemesterSelect && !valueCategorySelect){
                        allCoursesByStatus = coursesFilterBySemester;
                    }else if(valueCategorySelect && !valueSemesterSelect){
                        allCoursesByStatus = listCourseFilterByCategory;
                    }else if(valueCategorySelect && valueSemesterSelect){
                        allCoursesByStatus = listCourseFilterByCategory;
                    }
                }else{
                    if(!valueSemesterSelect && !valueCategorySelect){
                        allCoursesByStatus = getCourseByStatus(courseList, selected);
                    }else if(valueSemesterSelect && !valueCategorySelect){
                        allCoursesByStatus = getCourseByStatus(coursesFilterBySemester, selected);
                    }else if(valueCategorySelect && !valueSemesterSelect){
                        allCoursesByStatus = getCourseByStatus(listCourseFilterByCategory, selected);
                    }else if(valueCategorySelect && valueSemesterSelect){
                        allCoursesByStatus = getCourseByStatus(listCourseFilterByCategory, selected);
                    }
                }
                handleRenderFinal(allCoursesByStatus);
            })
        }
        handleStatusSelect();

        function handleRenderFinal(listCourse) {
            if(listCourse.length){
                //reset pagnumber correct with total course of semester
                $('.pagination__wrapper').twbsPagination('destroy');
                initPagination(listCourse, 10);
                courseTotal(listCourse.length, allHours(listCourse));
            }else{
                renderCourse(listCourse, courseDetailUrl, 0);
                courseTotal(listCourse.length, allHours(listCourse));
                $('.pagination__wrapper').hide();
            }
        }

        function formatConfigStatusSelect(dataFormat) {
            let arr = [];
            arr.push({
                value: 'all',
                label: 'All Status',
                selected: true
            })
            for (let i = 0; i< dataFormat.length; i++){
                let obj = {
                    value: dataFormat[i],
                    label: dataFormat[i]
                };
                arr.push(obj);
            }
            return arr;
        }

        function setStatusOption(configOption) {
            if($selectStatus.length) {
                statusSelection.clearChoices();
                statusSelection.setChoices(
                    configOption,
                    'value',
                    'label',
                    true,
                )
            }
        }
        let arrStatusOption = formatConfigStatusSelect(getStatusCourses(courseList));
        setStatusOption(arrStatusOption);

        let setSemesterOptionSelect = (configOption) => {
            if ($selectSemester.length) {
                semesterSelection.clearChoices();
                semesterSelection.setChoices(
                    configOption,
                    'value',
                    'label',
                    false,
                );
            }
        }
        setSemesterOptionSelect(formatSelectSemester(courseList))

        function setOptionSelectCategories(configOption) {
            selection.clearChoices();
            selection.setChoices(
                configOption,
                'value',
                'label',
                false,
            );
        }

        if ($('#dropdown-project').length) {
            selection.setChoices(
                formatConfigSelect(categories),
                'value',
                'label',
                false,
            );
        }

        function initPagination(data, itemPerPage) {
            let numberPage = data.length / itemPerPage;
            if (data.length % itemPerPage) {
                numberPage += 1;
            }
            $('.pagination__wrapper').twbsPagination({
                nextClass:'page-items next',                
                prevClass:'page-items prev',            
                lastClass:'page-items last',                
                firstClass:'page-items first',
                first: '&#171;',
                prev: '&#8249;',
                next: '&#8250;',
                last:'&#187;',
                totalPages: numberPage,
                visiblePages: 5,
                onPageClick: function (event, page) {                    
                    const startIndex = (page - 1) * 10;                        
                    const endIndex = (page * 10);
                    const listShowOnePage = data.slice(startIndex, endIndex);
                    renderCourse(listShowOnePage, courseDetailUrl, startIndex);
                }
            });
        }

        initPagination(courseList, 10);
        
    });
        
    
}, "courseList");